import React from "react";
import propTypes from "prop-types";
import styled from "styled-components";
import Loader from "Components/Loader";
import Section from "Components/Section";
import Message from "Components/Message";
import Poster from "Components/Poster";
import Helmet from "react-helmet";

const Container = styled.div`
  padding: 0px 20px;
`;

const Form = styled.form`
  margin-bottom: 50px;
  width: 100vw;
`;

const Input = styled.input`
  all: unset;
  font-size: 15px;
  width: 100vw;
`;

const SearchPresenter = ({
  movieResult,
  tvResult,
  searchTerm,
  handleSubmit,
  updatedTerm,
  error,
  loading
}) => (
  <Container>
    <Helmet>
      <title>Search | Steveflix</title>
    </Helmet>
    <Form onSubmit={handleSubmit}>
      {/* SearchContainerì•ˆì— ìˆëŠ” ì„œë¸Œë°‹, searchTermì— ë„£ì–´ì£¼ë©´ ì°¾ìŒ */}
      <Input
        placeholder="Search Movies or Tv Shows"
        value={searchTerm}
        onChange={updatedTerm}
      />
      {/* handleSubmitì•ˆì— searchTermì— ë‹´ì•„ì£¼ë©´ SearchContainer
        ê°€ Statesì— ë„£ê³  ë‹¤ì‹œ SearchPresenterë¡œ ì „ë‹¬í•´ì¤Œ 
        Inputì•ˆì— valueë¡œ í•´ë†”ì„œ ì¶”ì ê°€ëŠ¥í•¨ */}
      {/*
        onChangeë¡œ í•´ë†“ì€ ì—…ë°ì´íŠ¸ í•¨ìˆ˜ì— SyntheticEventê³„ì† ë“¤ì–´ê°
        */}
    </Form>
    {loading ? (
      <Loader />
    ) : (
      <>
        {/* movie show ì—¬ëŸ¬ ë¶€ë¥˜ ê²€ìƒ‰ê²°ê³¼ê°€ ë¦¬í„´ë˜ë‹ˆ fragmentí•„ìš” */}
        {movieResult && movieResult.length > 0 && (
          <Section title="Movie Results">
            {movieResult.map(movie => (
              <Poster
                key={movie.id}
                id={movie.id}
                imgUrl={movie.poster_path}
                title={movie.original_title}
                rating={movie.vote_average}
                year={movie.release_date && movie.release_date.substring(0, 4)}
                // nullë ë•Œ ë°©ì§€
              />
            ))}
          </Section>
        )}
        {tvResult && tvResult.length > 0 && (
          <Section title="TV Results">
            {tvResult.map(show => (
              <Poster
                key={show.id}
                id={show.id}
                show
                imgUrl={show.poster_path}
                title={show.original_name}
                rating={show.vote_average}
                year={
                  show.first_air_date && show.first_air_date.substring(0, 4)
                }
              />
            ))}
          </Section>
        )}
        {error && <Message text={error} color="red" />}
        {tvResult &&
          movieResult &&
          tvResult.length === 0 &&
          movieResult.length === 0 && (
            <Message text="Nothing found" color="grey" />
          )}
      </>
    )}
  </Container>
);

SearchPresenter.propTypes = {
  // propsì˜ ìë£Œí˜• ì •í•´ì¤ŒpropTypes
  movieResult: propTypes.array,
  tvResult: propTypes.array,
  searchTerm: propTypes.string,
  handleSubmit: propTypes.func.isRequired,
  loading: propTypes.bool.isRequired,
  error: propTypes.string,
  updatedTerm: propTypes.func.isRequired
};

export default SearchPresenter;
