import React from "react";
import TVPresenter from "./TVPresenter";
import { TVApi } from "api";

export default class extends React.Component {
  state = {
    topRated: null,
    airingToday: null,
    popular: null,
    error: null,
    loading: true
  };

  async componentDidMount() {
    // ì»´í¬ë„ŒíŠ¸ ì‹¤í–‰ëë‚¼ë•Œ
    try {
      const {
        data: { results: topRated }
      } = await TVApi.topRated(); // ë¦¬ìŠ¤í°ìŠ¤ ê¸°ë‹¤ë ¤ì¤˜!

      const {
        data: { results: airingToday }
      } = await TVApi.airingToday();

      const {
        data: { results: popular }
      } = await TVApi.popular();
      this.setState({
        topRated,
        airingToday,
        popular
      }); // Stateì— ë„£ì–´ì¤Œ
    } catch {
      this.setState({
        error: "can't find TVs infomation."
      });
    } finally {
      this.setState({
        loading: false // ì—ëŸ¬ê°€ ë‚˜ë“  ë¬´ë¹„ê°€ ë‚˜ì˜¤ë“ 
      });
    }
  }

  render() {
    const { topRated, airingToday, popular, error, loading } = this.state;

    return (
      <TVPresenter
        topRated={topRated}
        airingToday={airingToday}
        popular={popular}
        error={error}
        loading={loading}
      />
    );
  }
}
