import React from "react";
import SearchPresenter from "./SearchPresenter";
import { movieApi, TVApi } from "api";

export default class extends React.Component {
  state = {
    movieResults: null,
    tvResults: null,
    searchTerm: "",
    error: null,
    loading: false // ë””í´íŠ¸ë¡œ ì•„ë¬´ê²ƒë„ ë¡œë”© ì•ˆí•¨! ê²€ìƒ‰ê¸°ë‹¤ë¦¼
  };

  handleSubmit = event => {
    event.preventDefault(); // ì—”í„°ë¥¼ ì³ë„ ìë™ìœ¼ë¡œ submit&ì´ˆê¸°í™”ì•ˆë¨
    const { searchTerm } = this.state;
    if (searchTerm !== "") {
      this.searchByTerm();
    }
  };

  updatedTerm = event => {
    const {
      target: { value }
    } = event;
    console.log(value);
    this.setState({
      // ì´ê±°í•´ì•¼ í•œê¸€ìê°€ì•„ë‹ˆë¼ Presenterì—ì„œ ì—¬ëŸ¬ê¸€ì valueë¡œ ìŒ“ì—¬ê°
      searchTerm: value
    });
  };

  searchByTerm = async () => {
    const { searchTerm } = this.state;
    this.setState({ loading: true });
    try {
      const {
        data: { results: movieResults }
      } = await movieApi.search(searchTerm);

      const {
        data: { results: tvResults }
      } = await TVApi.search(searchTerm);
      this.setState({ movieResults, tvResults });
    } catch {
      this.setState({ error: "can't find results" });
    } finally {
      this.setState({ loading: false });
    }
  };

  render() {
    const { movieResults, tvResults, searchTerm, error, loading } = this.state;

    return (
      <SearchPresenter
        movieResult={movieResults}
        tvResult={tvResults}
        searchTerm={searchTerm}
        error={error}
        loading={loading}
        handleSubmit={this.handleSubmit}
        updatedTerm={this.updatedTerm}
      />
    );
  }
}
